lts\n  // fill the rest with scroll bar\n  display: flex;\n  flex-direction: column;\n  input {\n    $padding: 10px;\n    min-height: $searchItemHeight - (2 * $padding);\n    height: $searchItemHeight - (2 * $padding);\n    padding: $padding;\n  }\n\n  .search-results {\n    flex-grow: 1;\n    overflow-y: scroll;\n  }\n\n  // Vertically align text in search results by setting layout\n  // with flex and setting proper line-height\n  .search-results .search-result a {\n    display: flex;\n    height: $searchItemHeight;\n    .nickname {\n      flex-grow: 1;\n      vertical-align: middle;\n      line-height: $searchItemHeight;\n    }\n  }\n\n  .search-results {\n    .search-result {\n      // Fancy separator lines\n      border-bottom: 1px dashed #ddd;\n      box-sizing: border-box;\n\n      .avatar {\n        width: $searchItemHeight;\n        height: $searchItemHeight;\n        margin-right: 10px;\n      }\n    }\n  }\n\n  ul {\n    margin: 0;\n    padding: 0;\n  }\n\n  li {\n    list-style-type: none;\n    &.selected {\n      background-color: $hilightColor;\n    }\n  }\n}\n\n.main-content {\n  margin-left: $sidebarWidth;\n}\n\n.player {\n  display: flex;\n  flex-direction: row;\n  .player-avatar {\n    width: 170px;\n    height: 170px;\n  }\n  .player-info {\n    flex-grow: 1;\n    padding-left: $defaultPadding;\n  }\n}\n\n// Match component layout\n.match {\n  margin: ($defaultPadding / 2) 0;\n  padding: ($defaultPadding / 4) 0;\n  .match-header {\n    display: flex;\n    flex-direction: row;\n\n    .result {\n      width: 100px;\n    }\n    .match-name {\n      flex-grow: 1;\n      padding-left: $defaultPadding;\n    }\n    .timestamp {\n      width: 150px;\n      padding: 0 $defaultPadding;\n    }\n    .toggle {\n      width: 16px;\n      padding: 0 $defaultPadding;\n    }\n  }\n\n  .match-details {\n    .teams {\n      display: flex;\n      flex-direction: row;\n      .team {\n        flex-grow: 1;\n\n        display: flex;\n        flex-direction: row;\n        .avatar {\n          width: 240px;\n          height: 240px;\n        }\n\n        .players {\n          flex-grow: 1;\n          padding-left: $defaultPadding;\n        }\n      }\n    }\n  }\n}\n\n// Match component styles\n.match {\n  border: 1px solid #eee;\n  background: white;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n\n  .match-header {\n    .result {\n      text-align: center;\n      .win { color: green }\n      .loss { color: red }\n    }\n\n    .match-name {\n      border-left: 1px solid lightgray;\n      border-right: 1px solid lightgray;\n\n      .home-team {\n        text-decoration: underline;\n      }\n      .enemy-team {\n      }\n    }\n\n    .timestamp {\n      text-align: center;\n    }\n\n    .toggle {\n      text-align: center;\n      border-left: 1px solid lightgray;\n    }\n  }\n\n  .match-details {\n    padding: $defaultPadding;\n    border-top: 1px solid lightgray;\n  }\n}\n"]}]);
